import React, { Component } from 'react'  
import Clock from 'react-live-clock';  
import './lockdemo.css';  
export class ClockLiveDemo extends Component {  
        render() {  
                return (  
                        <div>  
                                <div className="container">  
                                       
                                        <div className="clk">  
                                                <Clock format={'HH:mm:ss'} ticking={true} />  
                                        </div>  
                                </div>  
                        </div>  
                )  
        }  
}  
  
export default ClockLiveDemo  