import React from 'react';
import './media.css';
import Example from './Example.js';

class Greet extends React.Component {
	
	constructor(props){
		super(props);
		
		this.state = {val: "Awesome"};
	}
	
	
	changeValue = () => {
		this.setState(
			{val: "Wonderful"}
			);
		}
	
	render(){
		return(
			<div id="h">
				<h1>Welcome to {this.state.val} World</h1>
				<div>
					<Example />
				</div>
				<button type="button" onClick={this.changeValue}>Welcome</button>

			</div>
		);
	}
}


export default Greet;