import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import './example.css';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
<link rel="stylesheet" href="<NODE_MODULES_FOLDER>/react-responsive-carousel/lib/styles/carousel.min.css" />
class Example extends Component {
    render() {
        return (
            <div align="center">
                <Carousel>
                <div>
                        <img src="assets/images/colo2.jpg" id="center" />
                    <p id="v" className="legend">Colosseum,Rome</p>
                 </div>
                <div>
                    <img src="assets/images/taj1.jpg" id="center"/>
                    <p id="v"  className="legend">Taj Mahal,India</p>
                </div>
                <div>
                    <img src="assets/images/wall3.jpg" id="center"/>
                    <p id="v" className="legend">Great Wall of China</p>
                </div>
                </Carousel>
                </div>
        );
    }
};
export default Example;